.has-tooltip {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: relative;
  /* &.small {
    white-space: normal;
    width: 100px;
  } */
}
.has-tooltip:after, .has-tooltip:before {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    z-index: 10;
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}
.has-tooltip:after {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    background: rgba(64, 64, 64, 0.8);
    border-radius: 5px;
    bottom: 120%;
    color: #fff;
    content: attr(data-tooltip);
    left: -30%;
    padding: 0.5rem 1rem;
    position: absolute;
    display: block;
    white-space: normal;
}
.has-tooltip:before {
    border: solid;
    border-color: rgba(64, 64, 64, 0.8) transparent;
    border-width: 6px 6px 0 6px;
    bottom: 100%;
    content: "";
    left: 40%;
    position: absolute;
}
.has-tooltip:hover:after, .has-tooltip:hover:before {
    visibility: visible;
    opacity: 1;
}
