  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  margin: 0 auto;
  max-height: calc(100vh - 40px);
  overflow: hidden;
  width: 100%;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
